*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;
}

body{
    background-color: #f28ab2;
}

.container{
    margin: 5rem auto;
    width: 60vh;
    height: auto;
    position: relative;
    background-color: white;
    border: 0.5px solid grey;
    box-shadow: 0px 0px 34px 2px #9B9B9B;
    border-radius: 5px;
    padding: 2rem;
}

.details{
    width: 50vh;
    display: inline-block;
    padding: 5px;
}

.details img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    float: left;
}

.detail-items {
  margin: 5px 50px;
  float: left;
}

.btn{
    width:50vh;
    padding: 0.75rem;
    background-color: #f28ab2;
    color: white;
    border-radius: 5px;
    border: none;
    font-size: 1.2rem;
}

.btn:hover{
    cursor: pointer;
    width:50vh;
    margin: 0 auto;
    padding: 0.80rem;
    transition: 2s font-size, 1s padding, 2s width;
    font-size: 1.4rem;
}